* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: #000;
  color: white;
  font-family: Arial, sans-serif;
}

.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30px 20px 80px 20px;
  background: linear-gradient(
    to bottom,
    rgba(0, 255, 0, 0.1),
    rgba(0, 0, 0, 0.95)
  );
  position: relative;
}

h1 {
  font-size: 4em;
  color: #0f0;
  text-align: center;
  margin-bottom: 10px;
  text-shadow: 
    0 0 5px #0f0,
    0 0 10px #0f0,
    0 0 20px #0f0;
  animation: 
    titleShake 1s ease-in-out,
    glow 2s ease-in-out infinite alternate;
}

.subtext {
  font-size: 1.2em;
  color: #666;
  margin-bottom: 20px;
  text-align: center;
}

.try-again-btn {
  padding: 20px 40px;
  font-size: 1.5em;
  background: #0f0;
  color: #000;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-weight: bold;
  transition: transform 0.3s ease;
  margin-bottom: 10px;
}

.try-again-btn:hover {
  transform: scale(1.1);
  animation: shake 0.4s ease-in-out infinite;
}

.meme-response {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  padding: 20px;
}

.meme-response h2 {
  color: #fff;
  font-size: 2em;
  text-align: center;
  text-shadow: 0 0 10px rgba(0, 255, 0, 0.3);
  animation: fadeInUp 0.5s ease-out forwards;
  opacity: 0;
  margin: 0;
}

.meme-container {
  width: 100%;
  height: 280px;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
}

.meme-image {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 10px;
  opacity: 0;
  animation: fadeInScale 0.5s ease-out forwards;
}

.meme-image:hover {
  transform: scale(1.02);
  transition: transform 0.3s ease;
  box-shadow: 0 0 30px rgba(0, 255, 0, 0.4);
}

.marquee-container {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(0, 0, 0, 0.9);
  overflow: hidden;
  padding: 15px 0;
  border-top: 1px solid rgba(0, 255, 0, 0.3);
  z-index: 1000;
}

.marquee {
  display: flex;
  width: fit-content;
  animation: scroll 30s linear infinite;
  white-space: nowrap;
}

.marquee span {
  color: white;
  font-size: 1.5em;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
  margin-right: 50px;
  transition: text-shadow 0.3s ease;
  animation: glow 2s ease-in-out infinite alternate;
}

@keyframes scroll {
  0% { transform: translateX(0); }
  100% { transform: translateX(-50%); }
}

@keyframes glow {
  from {
    text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
  }
  to {
    text-shadow: 0 0 10px rgba(0, 255, 0, 0.8),
                 0 0 20px rgba(0, 255, 0, 0.3);
  }
}

/* Add this for smooth text transitions */
.marquee span {
  opacity: 1;
  transition: opacity 0.5s ease;
}

.marquee.changing span {
  opacity: 0;
}

@keyframes shake {
  0%, 100% { transform: translate(0, 0) scale(1.1); }
  10%, 30%, 50%, 70%, 90% { transform: translate(-5px, 2px) scale(1.1); }
  20%, 40%, 60%, 80% { transform: translate(5px, -2px) scale(1.1); }
}

@media (max-width: 768px) {
  h1 {
    font-size: 2.5em;
  }
  
  .try-again-btn {
    padding: 15px 30px;
    font-size: 1.2em;
  }
  
  .meme-response h2 {
    font-size: 1.5em;
  }
  
  .meme-image {
    max-height: 300px;
  }
}

/* Update counter styles */
.counter-container {
  position: fixed;
  top: 20px;
  right: 20px;
  padding: 10px 20px;
  background: rgba(0, 0, 0, 0.8);
  border-radius: 10px;
  border: 1px solid rgba(0, 255, 0, 0.3);
  z-index: 10;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.counter-container:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(0, 255, 0, 0.3);
}

.counter-text {
  color: #0f0;
  font-size: 1.2em;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
  display: flex;
  align-items: center;
  gap: 5px;
}

.counter-number {
  font-weight: bold;
  font-size: 1.2em;
  min-width: 50px;
  display: inline-block;
  text-align: right;
}

/* Counter milestone animation */
@keyframes milestone {
  0% { transform: scale(1); }
  50% { transform: scale(1.2); }
  100% { transform: scale(1); }
}

.counter-container.milestone {
  animation: milestone 0.5s ease-in-out;
  box-shadow: 0 0 30px rgba(0, 255, 0, 0.8);
}

/* Add to your existing media queries */
@media (max-width: 768px) {
  .counter-container {
    top: 10px;
    right: 10px;
    padding: 8px 15px;
  }
  
  .counter-text {
    font-size: 1em;
  }
  
  .counter-number {
    font-size: 1.1em;
  }
}

/* Add/update these animation keyframes */
@keyframes titleShake {
  0%, 100% { transform: translate(0, 0) rotate(0deg); }
  10% { transform: translate(-5px, -2px) rotate(-1deg); }
  20% { transform: translate(5px, 2px) rotate(1deg); }
  30% { transform: translate(-5px, -2px) rotate(-1deg); }
  40% { transform: translate(5px, 2px) rotate(1deg); }
  50% { transform: translate(-5px, -2px) rotate(-1deg); }
  60% { transform: translate(5px, 2px) rotate(1deg); }
  70% { transform: translate(-5px, -2px) rotate(-1deg); }
  80% { transform: translate(5px, 2px) rotate(1deg); }
  90% { transform: translate(-5px, -2px) rotate(-1deg); }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeInScale {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

/* Social section styles */
.social-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 15px;
  margin: 0 0 20px 0;
}

.social-label {
  color: #0f0;
  font-size: 1.1em;
  text-shadow: 0 0 5px rgba(0, 255, 0, 0.5);
  font-weight: bold;
  letter-spacing: 0.5px;
  animation: glow 2s ease-in-out infinite alternate;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin: 5px 0;
}

.social-icon {
  width: 45px;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #0f0;
  border: 2px solid #0f0;
  border-radius: 50%;
  padding: 10px;
  transition: all 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  box-shadow: 0 0 10px rgba(0, 255, 0, 0.3);
  position: relative;
  overflow: hidden;
}

.social-icon::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: #0f0;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 1;
}

.social-icon svg {
  width: 100%;
  height: 100%;
  position: relative;
  z-index: 2;
  transition: transform 0.3s ease;
}

.social-icon:hover {
  transform: scale(1.15);
  box-shadow: 0 0 25px rgba(0, 255, 0, 0.8);
}

.social-icon:hover::before {
  opacity: 0.15;
}

.social-icon:hover svg {
  transform: scale(0.9);
}

/* Add this to your existing animations */
@keyframes float {
  0%, 100% { transform: translateY(0); }
  50% { transform: translateY(-5px); }
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .social-section {
    margin: 15px 0 25px 0;
    gap: 12px;
  }

  .social-label {
    font-size: 1em;
    text-align: center;
  }

  .social-icons {
    gap: 20px;
  }

  .social-icon {
    width: 40px;
    height: 40px;
    padding: 8px;
  }
}

/* For very small screens */
@media (max-width: 360px) {
  .social-icons {
    flex-direction: column;
    gap: 15px;
  }
}

/* Add fixed height container for response */
.response-container {
  width: 100%;
  height: 380px;
  max-width: 800px;
  position: relative;
  margin-top: -10px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .response-container {
    height: 330px;
    margin-top: -5px;
  }

  .meme-container {
    height: 230px;
  }
}

.sound-toggle {
  position: fixed;
  top: 20px;
  left: 20px;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  background: rgba(0, 0, 0, 0.8);
  border: 2px solid #0f0;
  color: #0f0;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  z-index: 1000;
  transition: all 0.3s ease;
}

.sound-toggle:hover {
  transform: scale(1.1);
  box-shadow: 0 0 15px rgba(0, 255, 0, 0.5);
}

.sound-toggle svg {
  width: 100%;
  height: 100%;
}

/* Mobile adjustments */
@media (max-width: 768px) {
  .sound-toggle {
    top: 10px;
    left: 10px;
    width: 35px;
    height: 35px;
  }
}